@import '_variables.sass';

body {
  padding: 0;
  margin: 0;
  color: #333;
  font-family: Arial; }

/* LAYOUT */

.container, #main {
  max-width: 999px;
  margin: 0 auto; }

header {
  height: 45px;
  background: #55C2E7;
  border-bottom: 1px solid #76A9BA; }

#logo {
  height: 37px;
  margin-top: 2px;
  border: 2px solid white;
  border-radius: 3px; }

aside {
  float: left;
  width: 200px;
  border-right: 1px solid #ccc;
  background: #efefef;
  ul {
    list-style: none;
    padding-left: 0;
    margin: 0;
    li {
      background: $heading_bg;
      a {
        display: block;
        padding: $padding 15px;
        text-shadow: 1px 1px 1px white;
        &.active {
          background: white;
          border: 1px solid #424242;
          border-right: none;
          margin-right: -1px; } } } } }

section#patients {
  position: relative;
  #patients-overview {
    overflow: auto; }
  #content {
    position: absolute;
    top: 0;
    right: 0;
    left: 200px;
    background: white;
    min-height: 500px; } }

table {
  width: 100%;
  border: 1px solid #ccc;
  margin: -1px;
  margin-bottom: 10px;
  thead {
    text-align: left;
    font-weight: bold;
    background: $heading_bg;
    th {
      padding: $padding;
      &.challenge-completion {
        width: 40%; }
      &.health-risk,
      &.activity-level {
        width: 30%; } } }
  tbody {
    tr {
      td {
        .bar {
          color: white;
          padding: 2px 4px;
          background: #EFEFEF;
          &.fitness {
            background: #F9BDD3; }
          &.diet {
            background: #73BDF4; }
          &.stress {
            background: #88D392; } } } } } }
/* LINKS */

a {
  color: inherit;
  text-decoration: none;
  &:link, &:active, &:visited {
    color: inherit;
    text-decoration: none; } }

/******************************************************************************
 * SECTIONS *
 ******************************************************************************/

/* Login */

#loginbox {
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  max-width: 200px;
  margin: 10px auto; }
